/*CSS DE LA PAGINA*/
.inicio-container {
    background-size: cover;
    background-position: center;
    min-height: 400px; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white; 
    text-align: center;
    padding: 50px; 
  }
  
  .inicio-container h2 {
    font-size: 50px;
    color: white;
  }

  .inicio-container p {
    font-size: 35px;
    color: white;
  }

/*FONDO DE LA PAGINA*/  
body {
    background-color: black;
}