body {
  background-color: black;
}

.MainContainer {
  display: flex;
  flex-direction: column;
}

.Container {
  display: flex;
  justify-content: space-between; /* Distribuye el espacio entre el texto y la imagen */
  margin-bottom: 20px; /* Espacio entre los contenedores */
}

.Text_container {
  flex: 1;
  margin-right: 10px; /* Espacio entre el texto y la imagen */
  color: white;
  font-size: 25px;
  margin-right: 10px;
}

.Content {
  text-align: left;
  font-size: 26px;
}

.Logo_container {
  flex: 1;
  margin-left: 10px; /* Espacio entre la imagen y el texto */
}

img {
  max-width: 500px;
  max-height: 350px;
  margin-top: 100px;
}


