.navbar {
  background-color: rgba(255, 255, 255, 0);
  overflow: hidden;
  display: flex;
  justify-content: center; 
  align-items: center;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: flex;
}

.navbar li {
  margin: 0 10px; 
}

.navbar li a {
  display: block;
  color: rgb(255, 255, 255);
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.navbar li a:hover {
  background-color: darkgray;
  color: white;
}

/* Media query para hacer responsive */
@media screen and (max-width: 600px) {
  .navbar ul {
    flex-direction: column; 
  }

  .navbar li {
    margin: 10px 0; 
  }
}
